import * as React from "react";
import ReactDOM from "react-dom";
import { useForm, SubmitHandler } from "react-hook-form";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Link, navigate } from "gatsby";
import Parse, { runCloudFunction } from "../vendor/ParseApp";
import Toast from "react-bootstrap/Toast";
import Tooltip from "react-bootstrap/Tooltip";

import Card from "react-bootstrap/Card";

enum GenderEnum {
  female = "female",
  male = "male",
  other = "other",
}

interface IFormInput {
  firstName: String;
  gender: GenderEnum;
}

export default function App() {
  const { register, handleSubmit } = useForm<IFormInput>();
  const onSubmit: SubmitHandler<IFormInput> = (data) => console.log(data);

  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({});

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  return (
    <Layout>
      <Container className="">
        <Row>
          <Col xs="0" md="3"></Col>
          <Col xs="12" md="6">
            <Card className="color-brand-1">
              <Card.Header>Login</Card.Header>

              <Card.Body>
                <Form>
                  <Row>
                    <Col xs="12">
                      <Form.Group
                        className="mb-3"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control
                          type="text"
                          placeholder=""
                          onChange={(v) =>
                            setModelValue("email", v.target.value)
                          }
                        />
                      </Form.Group>
                    </Col>
                    <Col xs="12">
                      <Form.Group
                        className="mb-3"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                          type="password"
                          placeholder=""
                          onChange={(v) =>
                            setModelValue("password", v.target.value)
                          }
                        />
                      </Form.Group>
                    </Col>
                  </Row>
                  <Button
                    variant="primary"
                    onClick={() => {
                      Parse.User.logIn(model.email, model.password)
                        .then((e) => {
                          navigate("/");
                        })
                        .catch((e) => {
                          setMessage(e?.message);
                        });
                    }}
                  >
                    Login
                  </Button>{" "}
                  <Button
                    variant="primary"
                    onClick={() => {
                      Parse.User.requestPasswordReset(model.email);
                    }}
                  >
                    Reset Password
                  </Button>
                  <div>{message}</div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
          <Col xs="0" md="3"></Col>
        </Row>
      </Container>
    </Layout>
  );
}
